import React, { Component } from "react";
import {Cell, Grid, ListItemContent, ListItem, List} from "react-mdl";

class Contact extends Component{
    render() {
        return(
        <div className="contact-body">
            <Grid className="contact-grid">
                <Cell col={6}>
                    <h2>Abdeljalil Bounaime</h2>
                    <img
                    src="https://cdn2.iconfinder.com/data/icons/avatar-2/512/Fred_man-512.png"
                    alt="avatar"
                    style={{height : '250px'}}
                    />
                    <p style={{width : '75%' , margin: 'auto' , paddingTop: '1em' }}>
                        Full Stack Web Developer using Java, JEE, React, Node.js with an emphasis on Frontend web development.
                    </p>
                </Cell>
                <Cell col={6}>
                    <h2>
                        Contact me
                    </h2>
                    <hr/>
                    <div className="contact-list">
                        <List>
                            <ListItem>
                                <ListItemContent style={{fontFamily : 'Anton', fontSize : '30px'}}>
                                    <i className='fas fa-mobile' aria-hidden= "true"/>
                                    (0033) 659-75-88-56
                                </ListItemContent>
                            </ListItem>

                            <ListItem>
                                <ListItemContent style={{fontFamily : 'Anton', fontSize : '30px'}}>
                                    <i className='fa fa-envelope' aria-hidden= "true"/>
                                    a.bounaime@gmail.com
                                </ListItemContent>
                            </ListItem>

                            <ListItem>
                                <ListItemContent style={{fontFamily : 'Anton', fontSize : '30px'}}>
                                    <i className='fab fa-skype' aria-hidden= "true"/>
                                    a.bounaime
                                </ListItemContent>
                            </ListItem>

                        </List>
                    </div>

                </Cell>

            </Grid>
        </div>
        )
    }
}
export  default  Contact;